@media screen and (max-width: 500px) {
  .tabs-style {
    max-width: 600px;
    width: 100vw;
    height: 50px;
    background-color: #64b3a5;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;
    position: fixed;
    top: 50px;
    font-family: "Voces" sans-serif;
    color: #ffffff;
  }
  .map-style, .flight-route-style {
    opacity: 0.7;
  }
  .single-tab {
    width: 180px;
    text-align: center;
  }
}



